.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.row{
  display: flexbox;
  flex-wrap: wrap;
}
.top-buffer {
  margin-top: 40px;
}
.bottom-buffer {
  margin-top: 40px;
}
ul{
  list-style-type: none;
}
  li {
    display: flexbox;
  }
.bothnav{
  background-color: rgb(53,75,88);
}
a.navbar-brand{
  color: white;
}